@use "./abstracts" as *;
.main {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

@media only screen and (min-width: 300px) {
  .main {
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media only screen and (min-width: 992px) {
  .main {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (min-width: 1280px) {
  .main {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.section {
  padding: 10px;
}
.container {
  border: 1px solid;
  border-radius: 36px;
  box-shadow: (2px 4px 27px rgba(0, 0, 0, 0.25));
  &:hover {
    box-shadow: 2px 4px 27px 1px #888888;
  }
  @include transition(box-shadow 0.2s ease);
  padding: 25px;
  min-height: 150px;
}

h1 {
  font-size: 36px;
  font-family: $bodyFont;
  text-align: left;
  line-height: 100%;
  margin-bottom: 10px;
}
p {
  font-size: 30px;
  font-family: $bodyFont;
  text-align: left;
  line-height: 100%;
}
